import React from "react";
import { BodyWrapper, GiftFooter, BrandLogo, Message, Desc, Form } from "..";

const GiftBodyInfoSender = ({ name, updateName, contact, updateContact }) => {
  return (
    <BodyWrapper>
      <BrandLogo />
      <Message contents='   !' />
      <Desc contents='     !' />
      <Form label='' field='sender_name' type='text' value={name} updateValue={updateName} />
      <Form label='' field='sender_contact' type='tel' value={contact} updateValue={updateContact} />
      {/* <GiftFooter before={before} next={next} /> */}
    </BodyWrapper>
  );
};

export default GiftBodyInfoSender;



