import React from "react";
import { BodyWrapper, BrandLogo, GiftFooter, Message, Desc, Form } from "..";

function GiftBodyInfoGift({ age, price, gender, updateAge, updateGender, updatePrice }) {
  return (
    <BodyWrapper>
    <BrandLogo />
      <Message contents="   !" />
      <Desc contents="   " />
      <Form label="" field="receiver_gender" type="button" vlaue={gender} updateValue={updateGender} />
      <Form label="" field="receiver_gender" type="button" vlaue={gender} updateValue={updateGender} />
      <Form label="" field="receiver_age" type="number" value={age} updateValue={updateAge} />
      <Form label="" field="gift_price" type="number" value={price} updateValue={updatePrice} />
      {/* <GiftFooter before={props.before} next={props.next} /> */}
    </BodyWrapper>
  );
}

export default GiftBodyInfoGift;



