import React from "react";
import { BodyWrapper, GiftFooter, BrandLogo, Message, Desc, Form } from "..";

function GiftBodyInfoReceiver({ name, updateName, contact, updateContact }) {

  return (
    <BodyWrapper>
      <BrandLogo />
      <Message contents="   !"/>
      <Desc contents="     !" />
      <Form label='' field='receiver_name' type='text' value={name} updateValue={updateName} />
      <Form label='' field='receiver_contact' type='tel' value={contact} updateValue={updateContact} />
      {/* <GiftFooter before={props.before} next={props.next} /> */}
    </BodyWrapper>
  );
}

export default GiftBodyInfoReceiver;



